body {
  background: #ffa133;
}
.text-center {
  text-align: center;
}
.slogan {
  // color: #fff;
}
#page-index {
  .content {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    width: 300px;
    text-align: center;
  }

  button{
    border: none;
    height: 38px;
    padding: 0 20px;
    margin: 5px;
    background-color: #000;
    color: #ddd;
    border-radius: 4px;
    font-size: 14px;
    &.ok,
    &.close {
      padding: 0;
      height: 30px;
      width: 60px;
    }
    &.ok {
    }
    &.close {
      background: #fff;
      border: 1px solid #000;
      color: #000;
    }
  }
}

.dialog-close {
  position: absolute;
  right: 10px;
  top: 2px;
}



