.preact-modal {
  display: none;
  position: fixed;
  background: rgba(0,0,0, 0.6);
  overflow: hidden;
  z-index: 10000;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  &.is-open {
    display: block;
  }
  .modal-close:before {
    font-family: 'Helvetica', Arial, sans-serif;
    cursor: pointer;
    background: transparent;
    color: #bbb;
    content: '\00D7';
    font-size: 25px;
    font-weight: 300;
    height: 30px;
    line-height: 25px;
    text-align: center;
    right: 3px;
    top: 3px;
    width: 30px;
  }
}
.preact-modal-content {
  width: 90%;
  max-width: 300px;
  border-radius: 4px;
  padding: 0;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  background: #fff;
  z-index: 10003;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);

  &.preact-modal-animate-open {

      // Something wrong in UC（表单输入框光标闪动）
      //-webkit-animation: scale-in 0.2s forwards;
      //@-webkit-keyframes scale-in {
      //    0% {
      //        -webkit-transform: scale(0);
      //    }
      //    100% {
      //        -webkit-transform: scale(1);
      //    }
      //}

      -webkit-animation: fadeIn 0.2s forwards;
      @-webkit-keyframes fadeIn {
          0% {
              opacity: 0;
          }
          100% {
              opacity: 1;
          }
      }
  }
  &.animate-close {
      -webkit-animation: scale-out 0.3s forwards;
      @-webkit-keyframes scale-out {
          0% {
              -webkit-transform: scale(1);
          }
          100% {
              -webkit-transform: scale(0);
          }
      }
  }
}

.preact-animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.preact-fade-in {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}



